/**
 * This injects Tailwind's base styles, which is a combination of
 * Normalize.css and some additional base styles.
 *
 * You can see the styles here:
 * https://unpkg.com/tailwindcss/dist/base.css
 */
@tailwind base;
/**
 * Remove the default box-shadow for invalid elements to prevent
 * inputs in Livewire components showing with a
 * red border by default in Firefox.
 *
 * See: https://github.com/laravel-frontend-presets/tall/issues/7
 */
input:invalid, textarea:invalid, select:invalid {
  box-shadow: none;
}

/**
 * This injects any component classes registered by plugins.
 */
@tailwind components;
/**
 * Here you would add any of your custom component classes; stuff that you'd
 * want loaded *before* the utilities so that the utilities could still
 * override them.
 *
 * Example:
 *
 * .btn { ... }
 * .form-input { ... }
 */
/**
 * This injects all of Tailwind's utility classes, generated based on your
 * config file.
 */
@tailwind utilities;
/**
 * Here you would add any custom utilities you need that don't come out of the
 * box with Tailwind.
 *
 * Example :
 *
 * .bg-pattern-graph-paper { ... }
 * .skew-45 { ... }
 */
[x-cloak] {
  display: none;
}

/* custom styles for modules */
* {
  outline: none;
}

.max-w-screen-xl {
  max-width: 1280px !important;
}

.pt-header {
  padding-top: 66px;
}

.top-header {
  top: 66px;
}

.h-screen-sidebar {
  height: calc(100vh - 66px);
}

.block-important {
  display: block !important;
}

@media all and (min-width: 1024px) {
  .w-initial {
    width: initial;
  }
}

.max-h-screen {
  max-height: calc(100vh - 100px);
}

.max-h-inherit {
  max-height: inherit;
}

html {
  scrollbar-color: theme("colors.indigo.600") #F5F5F5;
  scrollbar-width: thin;
}

html::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #F5F5F5;
}

html::-webkit-scrollbar {
  width: 10px;
  background-color: #F5F5F5;
}

html::-webkit-scrollbar-thumb {
  background-color: theme("colors.indigo.700");
  background-image: -webkit-linear-gradient(45deg, rgba(255, 255, 255, 0.2) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.2) 50%, rgba(255, 255, 255, 0.2) 75%, transparent 75%, transparent);
}

.scroll-thin {
  scrollbar-color: theme("colors.indigo.300") theme("colors.gray.100");
  scrollbar-width: thin;
}
.scroll-thin::-webkit-scrollbar-track {
  background-color: theme("colors.gray.100");
}
.scroll-thin::-webkit-scrollbar {
  width: 5px;
  background-color: theme("colors.gray.100");
}
.scroll-thin::-webkit-scrollbar-thumb {
  background-color: theme("colors.indigo.400");
  background-image: -webkit-linear-gradient(45deg, rgba(255, 255, 255, 0.2) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.2) 50%, rgba(255, 255, 255, 0.2) 75%, transparent 75%, transparent);
}

@media all and (max-width: 767px) {
  .tm-sidebar {
    position: fixed;
    z-index: 1;
    width: 230px;
    left: -230px;
    background: white;
    transition: left 300ms cubic-bezier(0.49, -0.01, 0.31, 0.93);
  }
  .tm-sidebar.tm-active {
    left: 0;
  }
}
.modal-body-height-fix {
  max-height: calc(100vh - 154px);
}